.App a div span{
  font-family: 'ProximaNova-Black';
  font-size: 20px;
}

h2 {
  font-size: 3vw;
}

h3 {
  font-size: 2vw;
}

p {
  font-size: 1.5vw;
  text-align: left;
}

.card span[role=img] {
  font-size: 4vw;
}

.emoji-text {
  margin: auto
}


.hero-component {
  grid-area: content;
  height: 100%;
  width: 100%
}

.header-name {
  font-size: 6vw;
  margin-bottom: 0;
  color: #100c08;
}

.side-bar {
  position: fixed;
  width: 30vw;
  height: 100vw;
  text-align: center;
}

.info {
  font-size: 2vw;
  padding: 1vw;
}

.name {
  font-size: 2vw;
  font-weight: bold;
  width: 100%;
  text-align: center;
}

.page {
  display: grid;
  grid-template-columns: auto;
  grid-template-areas: 'content'
}

.main-container {
  grid-area: content;
  overflow: auto;
  text-align: center;
  max-width: 100vw;
  margin-right: 10vw;
  margin-left: 10vw;
}

.card {
  background-color: #fff;
  border-radius: 8px;
  padding-bottom: 2%;
  padding-top: 1px;
  padding-left: 5%;
  padding-right: 5%;
  margin: 3vw;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.project {
  background-color: #f5f5f5;
  border-style: ridge;
  border-width: thin;
  border-radius: 30px;
  border-color: black;
  padding-top: 2%;
  margin: 3vw;
  box-shadow: 10px 10px 8px #888888;
}

.gif {
  width: 75%;
}

a:visited {
  color: #254E70;
}

#icon{
  padding: 10px;
  width: 5vw;
  transition: all .1s ease-in-out;
}

#icon:hover {
  transform: scale(1.11);
}

#image-container {
  border-radius: 100%;
}

#profile-photo {
  max-height: auto;
  max-width: 25vw;
  border-radius: 100%;
  margin-top: 1vh;
}

.job-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 33% auto;
}

.job-logo {
  grid-area: 1 / 1 / 2 / 2;
  padding: auto;
}
.job-title {
  grid-area: 1 / 2 / 2 / 5;
}
.job-description {
  grid-area: 2 / 2 / 3 / 5;
}
